import React from 'react';
import { createPortal } from 'react-dom';
import { AriaToastRegionProps } from '@react-aria/toast';
import { ToastQueue, useToastQueue } from '@react-stately/toast';

import ToastRegion from './ToastRegion';
import type { RegionPosition, ToastSettings } from './types';

export interface GlobalToastRegionProps<T extends ToastSettings = ToastSettings>
    extends AriaToastRegionProps {
    queue: ToastQueue<T>;
    className?: string;
    position?: RegionPosition;
}

const GlobalToastRegion = <T extends ToastSettings = ToastSettings>({
    queue,
    className,
    position = 'bottom-right',
    ...props
}: GlobalToastRegionProps<T>) => {
    const state = useToastQueue<ToastSettings>(queue);
    const { visibleToasts } = state;

    return visibleToasts.length > 0
        ? createPortal(
              <ToastRegion
                  {...props}
                  className={className}
                  position={position}
                  state={state}
              />,
              document.body
          )
        : null;
};

export default GlobalToastRegion;
