import React, { ReactNode } from 'react';
import { createPortal } from 'react-dom';
import { AriaToastRegionProps } from '@react-aria/toast';
import { ToastState, useToastState } from '@react-stately/toast';

import ToastRegion from './ToastRegion';
import type { RegionPosition, ToastSettings } from './types';

export interface ToastProviderProps<T extends ToastSettings = ToastSettings>
    extends AriaToastRegionProps {
    className?: string;
    maxVisibleToasts?: number;
    hasExitAnimation?: boolean;
    position?: RegionPosition;
    children: (state: ToastState<T>) => ReactNode;
}

const ToastProvider = <T extends ToastSettings = ToastSettings>({
    className,
    maxVisibleToasts,
    hasExitAnimation,
    position = 'bottom-right',
    children,
    ...props
}: ToastProviderProps<T>) => {
    const state = useToastState<T>({
        maxVisibleToasts,
        hasExitAnimation,
    });
    const { visibleToasts } = state;

    return (
        <>
            {children(state)}
            {visibleToasts.length > 0
                ? createPortal(
                      <ToastRegion
                          {...props}
                          className={className}
                          position={position}
                          state={state}
                      />,
                      document.body
                  )
                : null}
        </>
    );
};

export default ToastProvider;
