import React, { useRef } from 'react';
import type { AriaToastProps } from '@react-aria/toast';
import { useToast } from '@react-aria/toast';
import type { QueuedToast, ToastState } from '@react-stately/toast';
import classNames from 'classnames';

import { SvgClose, SvgError, SvgNotice, SvgSuccess } from '../Icons';

import CloseButton from './CloseButton';
import type { ToastSettings, Variant } from './types';

import styles from './ToastRegion.module.scss';

interface ToastProps<T> extends AriaToastProps<T> {
    state: ToastState<T>;
    toast: QueuedToast<T>;
}

const Toast = <T extends ToastSettings>({ state, ...props }: ToastProps<T>) => {
    const ref = useRef(null);
    const { toastProps, titleProps, descriptionProps, closeButtonProps } =
        useToast(props, state, ref);
    const { key, animation } = props.toast;
    const {
        title,
        description,
        variant = 'success',
        className,
    } = props.toast.content;

    const getVariantIcon = (variant: Variant) => {
        if (variant === 'success')
            return (
                <SvgSuccess className={styles.toast__icon} aria-hidden={true} />
            );
        if (variant === 'notice')
            return (
                <SvgNotice className={styles.toast__icon} aria-hidden={true} />
            );
        if (variant === 'error')
            return (
                <SvgError className={styles.toast__icon} aria-hidden={true} />
            );
        return;
    };

    const classList = classNames(
        className,
        styles.toast,
        styles[`toast--${variant}`]
    );

    return (
        <div
            {...toastProps}
            className={classList}
            ref={ref}
            data-animation={animation}
            onAnimationEnd={() => {
                if (animation === 'exiting') {
                    state.remove(key);
                }
            }}
        >
            {getVariantIcon(variant)}
            <div className={styles.toast__content}>
                <div {...titleProps} className={styles.toast__title}>
                    {title}
                </div>
                {description && (
                    <div
                        {...descriptionProps}
                        className={styles.toast__description}
                    >
                        {description}
                    </div>
                )}
            </div>
            <CloseButton {...closeButtonProps}>
                <SvgClose
                    className={styles['toast__close-icon']}
                    aria-hidden={true}
                />
            </CloseButton>
        </div>
    );
};

export default Toast;
