import React, { useRef } from 'react';
import { mergeProps, useFocusRing } from 'react-aria';
import type { AriaToastRegionProps } from '@react-aria/toast';
import { useToastRegion } from '@react-aria/toast';
import type { ToastState } from '@react-stately/toast';
import classNames from 'classnames';

import Toast from './Toast';
import type { RegionPosition, ToastSettings } from './types';

import styles from './ToastRegion.module.scss';

interface ToastRegionProps<T> extends AriaToastRegionProps {
    className?: string;
    state: ToastState<T>;
    position: RegionPosition;
}

const ToastRegion = <T extends ToastSettings>({
    className,
    state,
    position,
    ...props
}: ToastRegionProps<T>) => {
    const ref = useRef(null);
    const { regionProps } = useToastRegion(props, state, ref);
    const { isFocusVisible, focusProps } = useFocusRing();
    const { visibleToasts } = state;

    const classList = classNames(
        className,
        styles.toast__region,
        styles[`toast__region--${position}`],
        {
            [styles['toast__region--focused']]: isFocusVisible,
        }
    );

    return (
        <div
            {...mergeProps(regionProps, focusProps)}
            className={classList}
            ref={ref}
        >
            {visibleToasts.map((toast) => (
                <Toast key={toast.key} toast={toast} state={state} />
            ))}
        </div>
    );
};

export default ToastRegion;
